import React, { Component } from 'react';
import axios from 'axios';
import Menu from './Menu';

class Create extends Component {  

  constructor(props){    

    super(props);
  
    this.content = {
      header: "User Creation",
      content: "Content from state..."
    };
  
    this.state = {
      fullname:'', 
      username:'',
      output:''
    };
  
  }
  
  handleChange = (e) => {
    this.setState({
      [e.target.name]: e.target.value
    })
  }

  onSubmit = (e) => {

    let result="";

    e.preventDefault();
    const base_dir='http://172.16.31.78/reactapp/src/api/'
    const API_PATH = base_dir+'create-user.php';
    

    let formData = new FormData();
    formData.append('fullname', this.state.fullname)
    formData.append('username', this.state.username)

    axios({
      method: 'post',
      url: API_PATH,
      data: formData,
      config: { 
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
        }
      }
    })
        
    .then(response => {

      if (response.data[0]['result'] == 1) {           
        result=response.data[0]['message'];
        
        this.setState({
            fullname:'',
            username:'',
        })
        
        
      } else {
        result=response.data[0]['message'];
        this.setState({
            fullname:this.state.fullname,
            username:this.state.username
        })
      }

      this.setState({
          output: result 
      });            

    })
        
    .catch(function (response) {
        //handle error
        // console.log(response.data)
    });    
      
  }

    render() {
        return (
            <div>
              <Menu />
                <form>
                    <h4>{this.state.output}</h4>
                    <h1>{this.content.header}</h1>
                    <p>
                        Fullname <input type="text" name="fullname" value={this.state.fullname} onChange={e => this.handleChange(e)} autoFocus />
                    </p>
                    <p>
                        Username <input type="text" name="username" value={this.state.username} onChange={e => this.handleChange(e)} />
                    </p>
                    <p><input type="submit" name="submit" value="Create" onClick={(e) => this.onSubmit(e)} /></p>
                </form>
            </div>
        );
    }
}

export default Create;