import React, { Component } from 'react';
import { Redirect } from 'react-router-dom';
import axios from 'axios';
import App from './App';

class Login extends Component {

    constructor(props){    

        super(props);
    
        this.content = {
          header: "Login Page",
          content: "Content from state..."
        };
    
        this.state = { 
          username:'',
          password:'',
          output:''
        };
    
    }
    
    handleChange = (e) => {
        this.setState({
            [e.target.name]: e.target.value
        })
    }

    onSubmit = (e) => {

        let result="";
    
        e.preventDefault();
        const base_dir='http://172.16.31.78/reactapp/src/api/'
        const API_PATH = base_dir+'login.php';
        
    
        let formData = new FormData();
        formData.append('username', this.state.username)
        formData.append('password', this.state.password)
    
        axios({
            method: 'post',
            url: API_PATH,
            data: formData,
            config: { 
              headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
              }
            }
        })
            
        .then(response => {
            console.log(response)
          if (response.data[0]['result'] == 1) {
            this.props.history.push('/list');              
            // result=response.data[0]['message'];
            /*
            this.setState({
                username:'',
                password:'',
            })
            */
            
          } else {
            result=response.data[0]['message'];
            
          } 
            this.setState({
                output: result 
            });            
        })
            
        .catch(function (response) {
            //handle error
            // console.log(response.data)
        });
                
    }

    render() {


        return (
            <div>
                <form>
                    <h4>{this.state.output}</h4>
                    <h1>{this.content.header}</h1>
                    <p>
                        Username <input type="text" name="username" value={this.state.username} onChange={e => this.handleChange(e)} autoFocus />
                    </p>
                    <p>
                        Password <input type="password" name="password" value={this.state.password} onChange={e => this.handleChange(e)} />
                    </p>
                    <p><input type="submit" name="submit" value="Login" onClick={(e) => this.onSubmit(e)} /></p>
                </form>
                
            </div>
        );
    }
}

export default Login;